
.menu {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: right;
        justify-content: right;
  }
  
  .menu-activeButton {
    background-color :#266026c7 !important;
  }
  
  .menu-disableButton {
    background-color: #ff0000c7 !important;
  }
  
  .menu-text {
    display: inline !important;
  }
  
  .menu-item-info {
    -ms-flex: 1 1;
        flex: 1 1;
  }
  
  .menu-item-action {
    -ms-flex : 3 1;
        flex : 3 1;
    text-align: center;
    -ms-flex-pack: center !important;
        justify-content: center !important;
  }

  .drawer-list {
      padding: 10px !important;
  }

  .menu-drawer {
      width: 100%;
      max-width: 500px;
  }

  @media all and (max-width:750px) {
    .menu-invertButton {
      display: none !important;
    }
  }
  


